import layoutHeaderAside from '@/layout/header-aside'

// webpack
const _import = require('@/libs/util.import.' + process.env.NODE_ENV)

const meta = { auth: true }

export default {
  path: '/demo/frame',
  name: 'demo-frame',
  meta,
  redirect: { name: 'demo-frame-index' },
  component: layoutHeaderAside,
  children: (pre => [
    { path: 'index', name: `${pre}index`, component: _import('demo/frame/index'), meta: { ...meta, title: 'Frame ' } },
    { path: 'html', name: `${pre}html`, component: _import('demo/frame/html'), meta: { ...meta, title: ' HTML' } },
    { path: 'd2-doc', name: `${pre}d2-doc`, component: _import('demo/frame/d2-doc'), meta: { ...meta, title: 'D2Admin ' } }
  ])('demo-frame-')
}
